body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.box{
width:100%;
height:100vh;
background-size: cover;
background-repeat: no-repeat;
background-position: center center;
position:relative
}
.box-button{
background-color: #2872fa;
height:50px  ;
padding:12px 16px;
box-sizing: border-box;
display: flex;
align-items: center;
justify-content: center;
color:white;
position:absolute;
left:0;
right:0;
text-align: center;
margin:auto;
width: 230px;
border-radius: 5px;
bottom:110px
}
.searchbar {
  width:100%;
  position:fixed;
  z-index: 1;
  color:black;
  padding:0px 20px 0px 20px;
  box-sizing: border-box;
  height:80px;
  background-color: white;
  display:flex;
  justify-content: center;
  align-items: center;
}
.searchbar div{
  width:50%;
  background-color: rgba(245, 237, 237, 0.644);
  height:70%;
  display:flex;

}
.searchbar input{
  border:none;
  width:100%;
  background-color:  rgba(245, 237, 237, 0.644);
  font-size: x-large;
  padding:5px 15px 5px 15px;
  box-sizing: border-box;
}
.searchbar input:focus{
  outline:none;
}
.searchbar > i{
  position:fixed;
  top:23px;
  bottom:23px;
  right:20px;
  font-size: 30px;
  z-index: 1;
  cursor: pointer;
}
.navbar{
  width:100%;
  position:fixed;
  z-index: 1;
  color:black;
  padding:0px 20px 0px 20px;
  box-sizing: border-box;
  display: flex;
  justify-content: space-between;
  height:90px;
}
.navbar div{
display:flex;
height:100%;
flex-basis:270px;
align-items: center;
font-size:x-large
}
.navbar:hover{
 background-color: white;
}
 .div2{
  justify-content:center  ;
  text-align: center ;
  font-weight: bolder;
  font-size: xx-large !important;
}
.div3{
  justify-content: space-evenly 
}
.div1{
  justify-content: space-evenly;
}
.div3 span:nth-child(3){
  display:none
}
.div1 > i{
  display:none
}
@media only screen and (max-width: 800px) {
  .div3 span:nth-child(3){
    display:block;
    justify-content: center;
    
  }
  .div3 span:not(.div3 span:nth-child(3)){
    display:none
  }
  .div1 span{
   display:none
  }
  .div1 > i{
    display: block
  }
 .div3 {
  justify-content: flex-end;
 }
 .div1{
  justify-content: flex-start;
 }
 .searchbar div{
  width:80%
 }
}